import { Injectable } from '@angular/core';
import {Http, Response} from '@angular/http';
import 'rxjs/Rx';

@Injectable()
export class BookService {

  constructor(private http: Http) { }

  getAllBooks() {
    return this.http.get('books/10').map((response: Response) => response.json());
  }

  getAllReactiveBooks() {
    return this.http.get('books/reactive/10').map((response: Response) => response.json());
  }

}



